.radio-demo-wrapper .radio-content {
  overflow: auto;
}
.radio-demo-wrapper .radio-content .radio-item {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px;
  border-bottom: 1px solid #f0f1f2;
  white-space: 10px;
}
.radio-demo-wrapper .radio-content .radio-item .radio-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 18px;
  text-align: left;
  margin-right: 24px;
}
.radio-demo-wrapper .radio-content .radio-item .radio-demo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100px;
}
